import React from 'react';
import * as THREE from 'three';
import { PCDLoader } from 'three/examples/jsm/loaders/PCDLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

import Zaghetto from '../assets/zaghetto.pcd';

const Wallpaper = props => {
  const ref = React.useRef();

  React.useEffect(() => {
    const scene = new THREE.Scene();

    const camera = new THREE.PerspectiveCamera(
      15,
      ref.current.offsetWidth / ref.current.offsetHeight,
      0.01,
      40
    );
    camera.scale.set(1, -1, 1);
    camera.position.set(0, 0.5, 1.6);
    camera.updateProjectionMatrix();

    scene.add(camera);

    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(ref.current.offsetWidth, ref.current.offsetHeight);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.autoRotate = true;
    controls.autoRotateSpeed = 0.8;

    const loader = new PCDLoader();
    loader.load(Zaghetto, points => {
      points.position.set(-0.85, -0.63, 0);
      points.material.color.setHex(0x3f00ff);
      scene.add(points);
      const center = points.geometry.boundingSphere.center;
      controls.target.set(center.x - 0.85, center.y - 0.61, center.z);
      controls.update();
    });

    let animationId;
    const animate = () => {
      animationId = requestAnimationFrame(animate);
      controls.update();
      renderer.render(scene, camera);
    };
    animate();

    const observer = new ResizeObserver(() => {
      camera.aspect = ref.current.offsetWidth / ref.current.offsetHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(ref.current.offsetWidth, ref.current.offsetHeight);
    });
    observer.observe(ref.current);

    ref.current.appendChild(renderer.domElement);

    return () => {
      cancelAnimationFrame(animationId);
      observer.disconnect();
    };
  }, []);

  return <div ref={ref} {...props}></div>;
};

export default Wallpaper;
